.vertical-center {
  display: flex;
  align-items: center;
}

.horizontal-center {
  display: flex;
  justify-content: center;
}

.groups {
  display: flex;
  flex-direction: column;
}

.group {
  margin-bottom: 0.5em;
  margin-left: 3px;
  width: 100vw;
}

.group__info {
  height: 75px;
  flex-direction: row;
  font-size: 0.7em;
}

.shadow {
  filter: drop-shadow(0 0 3px rgba(0,0,0,0.5));
}

.icon {
  width: 50px;
  height: 50px;
  border-radius: 50%;
  background-size: auto 50px;
}
